import React from "react";

// reactstrap components
import { Card, CardBody } from "reactstrap";

// Core Components

function BlogCard7() {
  return (
    <>
      <Card className="card-blog bg-info card-lift--hover shadow">
        <img
          alt="..."
          className="img pattern rounded"
          src={require("assets/img/ill/p7.svg").default}
        ></img>
        <CardBody>
          <h4 className="display-4 text-white">7번</h4>
          <p className="lead text-white mt-0">
            7번
          </p>
        </CardBody>
      </Card>
    </>
  );
}

export default BlogCard7;
