import React from "react";

// reactstrap components
import { Button, Card, CardBody, CardTitle } from "reactstrap";

// Core Components

function PricingCard4() {
  return (
    <>
      <Card
        className="card-pricing bg-default card-background"
        data-background="image"
        style={{
          backgroundImage:
            "url(" + require("assets/img/ill/pricing_bg.svg").default + ")",
        }}
      >
        <CardBody>
          <h6 className="card-category text-white text-uppercase">긴급</h6>
          <CardTitle tag="h1">
            109,000<small className="text-white">￦</small>
          </CardTitle>
          <ul>
            <li>
              <b>1</b>
              대량 분리수거
            </li>
            <li>
              <b>2</b>
              옷감정리
            </li>
            <li>
              <b>3</b>
              가구설치
            </li>
            <li>
              <b>4</b>
              내용
            </li>
          </ul>
          <Button
            className="btn-white mt-3"
            color="default"
            href="#pablo"
            onClick={(e) => e.preventDefault()}
            size="sm"
          >
            쪽지보내기
          </Button>
        </CardBody>
      </Card>
    </>
  );
}

export default PricingCard4;
