import React from "react";

// reactstrap components
import { Card, CardHeader, CardBody, CardImg, CardTitle } from "reactstrap";

// Core Components

function BlogCard5() {
  return (
    <>
      <Card className="card-blog card-lift--hover shadow" data-header="skew">
        <CardHeader className="bg-yellow">
          <div className="card-image">
            <CardImg
              alt="..."
              src={require("assets/img/ill/inn.svg").default}
              top
            ></CardImg>
          </div>
        </CardHeader>
        <CardBody>
          <h6 className="card-category">
            {/* <i className="ni ni-paper-diploma"></i> */}
            5번
          </h6>
          <CardTitle tag="h5">
            <a href="#pablo" onClick={(e) => e.preventDefault()}>
              블로그형5번
            </a>
          </CardTitle>
          <p className="card-description">
            블로그형 5번
          </p>
        </CardBody>
      </Card>
    </>
  );
}

export default BlogCard5;
