import React from "react";

// reactstrap components
import { Card, CardBody, CardImg, CardTitle, CardText } from "reactstrap";

// Core Components

function BlogCard14() {
  return (
    <>
      <Card className="card-lift--hover shadow">
        <CardImg
          alt="..."
          src={require("assets/img/ill/linth3.svg").default}
          top
        ></CardImg>
        <CardBody>
          <h6 className="card-category text-danger">
            <i className="ni ni-badge"></i> head
          </h6>
          <CardTitle className="text-success" tag="h5">
            <a href="#pablo" onClick={(e) => e.preventDefault()}>
              제목
            </a>
          </CardTitle>
          <CardText className="mt-4">
            파프리카 카드 내용
          </CardText>
        </CardBody>
      </Card>
    </>
  );
}

export default BlogCard14;
