import React from "react";

// reactstrap components
import {
  Badge,
  Card,
  CardBody,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  Table,
} from "reactstrap";

// Core Components

function ProfileCard6() {
  return (
    <>
      <Card className="card-profile card-lift--hover shadow" data-background="full">
        <a href="#pablo" onClick={(e) => e.preventDefault()}>
          <img
            alt="..."
            className="img img-raised rounded"
            src={require("assets/img/theme/lucy.jpg").default}
          ></img>
        </a>
        <div className="card-image">
          <UncontrolledDropdown>
            <DropdownToggle
              aria-expanded={false}
              caret
              className="btn-icon-only"
              color="link"
              data-toggle="dropdown"
              type="button"
            >
              <i className="ni ni-settings-gear-65"></i>
            </DropdownToggle>
            <DropdownMenu right x-placement="bottom-end">
              <DropdownItem href="#pablo" onClick={(e) => e.preventDefault()}>
                프로필
              </DropdownItem>
              <DropdownItem href="#pablo" onClick={(e) => e.preventDefault()}>
                즐겨찾기
              </DropdownItem>
              <DropdownItem href="#pablo" onClick={(e) => e.preventDefault()}>
                쪽지보내기
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
        </div>
        <CardBody>
          <h4 className="display-4">닉네임</h4>
          <p className="lead mt-0 mb-1">123</p>
          <Table className="tablesorter" responsive>
            <tbody>
              <tr>
                <td className="text-left pl-0">
                  <Badge className="badge-circle mr-2" color="info">
                    <i className="ni ni-hat-3"></i>
                  </Badge>
                  서울
                </td>
                <td className="text-right">ㅇㅇ</td>
              </tr>
              <tr>
                <td className="text-left pl-0">
                  <Badge className="badge-circle mr-2" color="info">
                    <i className="ni ni-satisfied"></i>
                  </Badge>
                  청소
                </td>
                <td className="text-right">ㅇㅇ</td>
              </tr>
              <tr>
                <td className="text-left pl-0">
                  <Badge className="badge-circle mr-2" color="info">
                    <i className="ni ni-bullet-list-67"></i>
                  </Badge>
                  예산
                </td>
                <td className="text-right">•50000</td>
              </tr>
            </tbody>
          </Table>
        </CardBody>
      </Card>
    </>
  );
}

export default ProfileCard6;
