import React from "react";

// reactstrap components
import { Button, Card, CardBody, CardFooter, CardTitle } from "reactstrap";

// Core Components

function ProfileCard3() {
  return (
    <>
      <Card className="card-profile card-lift--hover shadow" data-image="img-raised">
        <div className="card-header-image">
          <a href="#none" onClick={(e) => e.preventDefault()}>
            <img
              alt="..."
              className="img"
              src={require("assets/img/faces/alejandro-escamilla.jpg").default}
            ></img>
          </a>
          <CardTitle className="text-white" tag="div">
            2021-05-03
          </CardTitle>
        </div>
        <CardBody>
          <h6 className="card-category text-info text-center">
            제목
          </h6>
          <p className="card-description">
            내용
          </p>
        </CardBody>
        <CardFooter className="text-center">
          <Button
            className="btn-icon-only btn-simple btn-default"
            color="instagram"
            type="button"
          >
            <span className="btn-inner--icon">
              <i className="fab fa-instagram"></i>1
            </span>
          </Button>
          <Button
            className="btn-icon-only btn-simple"
            color="twitter"
            type="button"
          >
            <span className="btn-inner--icon">
              <i className="fab fa-twitter"></i>2
            </span>
          </Button>
          <Button
            className="btn-icon-only btn-simple"
            color="dribbble"
            type="button"
          >
            <span className="btn-inner--icon">
              <i className="fab fa-dribbble"></i>3
            </span>
          </Button>
        </CardFooter>
      </Card>
    </>
  );
}

export default ProfileCard3;
