import React from "react";

// reactstrap components
import { Button, Modal, InputGroupAddon, InputGroupText } from "reactstrap";
import Chat from "components/chat/Chat";
// Core Components

function ModalChat() {
  const [modalOpen, setModalOpen] = React.useState(false);
  const [messageFocus, setMessageFocus] = React.useState("");
  return (
    <>
      <Button
        className="card-profile-actions"
        color="white"
        onClick={() => setModalOpen(!modalOpen)}
        type="button"
      >
        채팅하기
      </Button>
      <Modal
        isOpen={modalOpen}
        toggle={() => setModalOpen(!modalOpen)}
        contentClassName="bg-kakao"
        className="modal-dialog-centered modal-light"
      >
        <div className="modal-header">
          <h6 className="modal-title" id="modal-title-notification">
            닉네임님과 채팅
          </h6>
          <button
            aria-label="Close"
            className="close"
            onClick={() => setModalOpen(!modalOpen)}
            type="button"
          >
            <span aria-hidden={true}>×</span>
          </button>
        </div>
        <div className="modal-body">
          <Chat />
        </div>
        <div className="modal-footer">
          <textarea 
          className="form-control autosize" 
          name="message" 
          rows="2" 
          id="replyInputMessage" 
          placeholder="메시지 입력"
          onFocus={() => setMessageFocus("focused")}
          onBlur={() => setMessageFocus("")}
          ></textarea>
          <InputGroupAddon addonType="append">
            <InputGroupText>
             전송
            </InputGroupText>
          </InputGroupAddon>
          {/* <Button className="btn-white" color="default" type="button">
            Ok
          </Button>
          <Button
            className="text-white ml-auto"
            color="link"
            onClick={() => setModalOpen(!modalOpen)}
            type="button"
          >
            Close
          </Button> */}
        </div>
        
      </Modal>
      
    </>
  );
}

export default ModalChat;
