class Token {
  
  token;
  accesstoken;
  refreshtoken;

  constructor(Jsondata){
    this.token =  JSON.parse(JSON.stringify(Jsondata))
    this.accesstoken  = this.token.accesstoken;
    this.refreshtoken = this.token.refreshtoken;
    console.log('accesstoken : '+this.accesstoken)
    console.log('refreshtoken : '+this.refreshtoken)
  }
}

class UserAPI {

  #email='';;
  // #username='';
  #password='';
  #repassword='';
  #token;
  history;
  dispatch;
  
  #URL='http://13.209.84.225:8000/users/';

  constructor(email, password) {
    this.#email    = email;
    // this.#username = username;
    this.#password = password;
  }

  componentWillMount() {
    this.state =  { token: cookie.load('token') }
  }

  setEmail(email)
  {
    this.#email= email
    console.log("email: "+this.#email)
  }

  setPassword(password)
  {
    this.#password= password
    console.log("password: "+this.#password)
  }

  setrePassword(repassword)
  {
    this.#repassword= repassword
    console.log("password: "+this.#repassword)
  }

 rePasswordck() {
    if (this.#password !== this.#repassword) {
      <div>hello</div>
    }
  }
  
  signCheck() {
    let email_RegExp = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
    let RegExp = /^[a-zA-Z0-9]{4,20}$/; //id와 pwassword 유효성 검사 정규식
    if(this.#email == '') { // 이메일 입력여부 검사
      alert("이메일을 입력해주세요.");
      return true;
    }
    if(!email_RegExp.test(this.#email)) { //이메일 유효성 검사
      alert("올바른 이메일 형식이 아닙니다.");
      return true;
    }    
    if(this.#password == ''){ // 비밀번호 입력여부 검사
      alert("비밀번호를 입력해주세요.");
      return true;
    }
    if(!RegExp.test(this.#password)){ //패스워드 유효성검사
      alert("비밀번호는 4자이상의 영문 대소문자와 숫자로만 입력하여 주세요.");
      return true;
    }
  }

  signIn = async () => {

    if((this.signCheck(this.#email,this.#password)) || !(this.#email && this.#password)) {
      return;
    }
    else {
    const tokendata = await axios.post(this.#URL+"token/", {
      email  : this.#email,
      password  : this.#password      
      
    },{
      'Content-type' : 'application/json',
      // 'Authorization' : 'JWT fefege...'
      'Access-Control-Allow-Origin': '*'
  })
    .then((tokendata)=> {

      console.log(tokendata);

      sessionStorage.setItem("sessionaccesstoken",JSON.stringify(tokendata.data))


      this.#token = new Token(tokendata.data);

      alert(this.#email+'님 환영합니다.');
      
      // window.location.href = '/RegisterPage'; // 로그인페이지로 새로고침
    })

    .catch((error) => {
      console.log(error);
        alert('잘못된 계정 정보입니다.');
      return Promise.reject(error.response);
    })

 
    }
    // cookie.save(
    //   this.#email+': refreshtoken',
    // {
    //   value: this.#token.refreshtoken,
    //   domain: this.#URL+'token/',
    //   path: '/',
    //   maxAge: 1000,
    //   secure: false,
    //   httpOnly: true
    // })
    // window.location.href = '/'; // 홈페이지로 새로고침 
  }

  signUp = async () => {

    if((this.signCheck(this.#email,this.#password)) || !(this.#email && this.#password) || !(this.#password == this.#repassword)) 
    {
      return;
    } 
    else {    
      const response = await axios.post(this.#URL , {
        email  : this.#email,
        password  : this.#password
      }).then(()=> {
        alert(this.#email+"님 가입을 축하드립니다.");
      }).catch((error) => {
        console.log("asd");
        if (error.response.status == 400) {
          alert('이미 가입된 아이디입니다.');
        }
      })  
}
  }
       
  refresh = async () => {
    const respone = await axios.post(this.#URL+"respone/", {
      refresh: this.#token.refreshtoken
    })
    this.#token.accesstoken = respone.#token.accesstoken
  }
};
export {UserAPI, Token};
