import React from "react";

// reactstrap components
import { Card, CardHeader, CardBody, CardFooter } from "reactstrap";

// Core Components

function PricingCard2() {
  return (
    <>
      <Card className="card-pricing bg-white border-0 text-center mb-4">
        <CardHeader className="bg-transparent">
          <h6 className="text-uppercase ls-1 py-3 mb-0">긴급카드</h6>
        </CardHeader>
        <CardBody>
          <div className="display-2">89,000원</div>
          <span>2021-05-04까지</span>
          <ul className="list-unstyled my-4">
            <li className="align-items-center">
              <b className="text-primary">#태그</b>
              <span>내용</span>
            </li>
            <li className="align-items-center">
              <b className="text-primary">#태그</b>
              <span>내용</span>
            </li>
            <li className="align-items-center">
              <b className="text-primary">#태그</b>
              <span>내용</span>
            </li>
          </ul>
        </CardBody>
        <CardFooter className="bg-transparent">
          <a href="#pablo" onClick={(e) => e.preventDefault()}>
            자세히 보기
          </a>
        </CardFooter>
      </Card>
    </>
  );
}

export default PricingCard2;
