import React from "react";

// reactstrap components
import { Button, Card, CardBody } from "reactstrap";

// Core Components

function BlogCard12() {
  return (
    <>
      <Card className="card-blog bg-default card-lift--hover shadow">
        <img
          alt="..."
          className="img pattern rounded"
          src={require("assets/img/ill/p7.svg").default}
        ></img>
        <CardBody>
          <h4 className="display-4 text-white">12번</h4>
          <p className="lead text-white mt-0">
            카드 12번
            파프리카 파프리카 파프리카 파프리카 파프리카 파프리카 파프리카 파프리카 
          </p>
          <Button
            className="text-white px-0"
            color="link"
            href="#"
            onClick={(e) => e.preventDefault()}
          >
            자세히 보기
          </Button>
        </CardBody>
      </Card>
    </>
  );
}

export default BlogCard12;
