import React from "react";

// reactstrap components
import {
  Badge,
  Button,
  Media,
  Pagination,
  PaginationItem,
  PaginationLink,
  Progress,
  Table,
  UncontrolledTooltip,
} from "reactstrap";

function Table3() {
  return (
    <>
      <Table className="align-items-center table-flush" responsive>
        <thead className="thead-light">
          <tr>
            <th className="sort" data-sort="status" scope="col" width="20%">
              상태
            </th>
            <th className="sort" data-sort="completion" scope="col">
              완성
            </th>
          </tr>
        </thead>
        <tbody className="list">
          <tr>
            <td width="20%">
              <Badge className="badge-dot mr-4">
                <i className="bg-warning"></i>
                <span className="status">친절</span>
              </Badge>
            </td>
            <td>
              <div className="d-flex align-items-center">
                <span className="completion mr-2">60%</span>
                <div>
                  <Progress color="warning" max="100" value="60"></Progress>
                </div>
              </div>
            </td>   
          </tr>
          <tr>
            <td>
              <Badge className="badge-dot mr-4">
                <i className="bg-success"></i>
                <span className="status">감동</span>
              </Badge>
            </td>
            <td>
              <div className="d-flex align-items-center">
                <span className="completion mr-2">100%</span>
                <div>
                  <Progress color="success" max="100" value="100"></Progress>
                </div>
              </div>
            </td>
          </tr>
          <tr>
            <td>
              <Badge className="badge-dot mr-4">
                <i className="bg-danger"></i>
                <span className="status">빠름</span>
              </Badge>
            </td>
            <td>
              <div className="d-flex align-items-center">
                <span className="completion mr-2">72%</span>
                <div>
                  <Progress color="danger" max="100" value="72"></Progress>
                </div>
              </div>
            </td>       
          </tr>
          <tr>
            <td>
              <Badge className="badge-dot mr-4">
                <i className="bg-info"></i>
                <span className="status">자비</span>
              </Badge>
            </td>
            <td>
              <div className="d-flex align-items-center">
                <span className="completion mr-2">20%</span>
                <div>
                  <Progress color="info" max="100" value="20"></Progress>
                </div>
              </div>
            </td>
          </tr>
          <tr>
            <td>
              <Badge className="badge-dot mr-4">
                <i className="bg-success"></i>
                <span className="status">신뢰도</span>
              </Badge>
            </td>
            <td>
              <div className="d-flex align-items-center">
                <span className="completion mr-2">100%</span>
                <div>
                  <Progress color="success" max="100" value="100"></Progress>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </Table>
    </>
  );
}

export default Table3;
