import React from "react";

// reactstrap components
import { Card, CardBody, CardFooter, CardTitle } from "reactstrap";

// Core Components

function BlogCard3() {
  return (
    <>
      <Card className="card-blog card-lift--hover shadow" data-header="pattern">
        <a href="#pablo" onClick={(e) => e.preventDefault()}>
          <img
            alt="..."
            className="img pattern rounded"
            src={require("assets/img/ill/p2.svg").default}
          ></img>
        </a>
        <CardBody>
          <h6 className="card-category text-danger">
            <i className="ni ni-badge"></i>
            head
          </h6>
          <CardTitle tag="h5">
            <a href="#pablo" onClick={(e) => e.preventDefault()}>
              블로그형3
            </a>
          </CardTitle>
          <p className="card-description">
            블로그형 카드 3번
          </p>
        </CardBody>
        <CardFooter>
          <div className="author">
            <img
              alt="..."
              className="avatar img-raised"
              src={require("assets/img/faces/team-3.jpg").default}
            ></img>
            <span>닉네임</span>
          </div>
          <div className="stats stats-right">
          <i class="fa fa-heart" aria-hidden="true"></i>
            10.4K · <i className="ni ni-archive-2"></i>
            425
          </div>
        </CardFooter>
      </Card>
    </>
  );
}

export default BlogCard3;
