import React from "react";

// reactstrap components
import { Button, Card, CardBody, CardTitle } from "reactstrap";

// Core Components

function ProfileCard4() {
  return (
    <>
      <Card className="card-profile card-lift--hover shadow" data-image="img-rounded">
        <div className="card-avatar">
          <a href="#" onClick={(e) => e.preventDefault()}>
            <img
              alt="..."
              className="img"
              src={require("assets/img/faces/team-1.jpg").default}
            ></img>
          </a>
        </div>
        <CardBody>
          <h6 className="card-category text-gray">20201-05-03</h6>
          <CardTitle tag="h4">제목</CardTitle>
          <p className="card-description">
            내용
          </p>
          <Button
            className="btn-round"
            color="info"
            href="#"
            onClick={(e) => e.preventDefault()}
          >
            채팅
          </Button>
        </CardBody>
      </Card>
    </>
  );
}

export default ProfileCard4;
