import React from "react";

// reactstrap components
import { Card, CardBody, CardTitle } from "reactstrap";

// Core Components

function BlogCard15() {
  return (
    <>
      <Card className="card-blog card-lift--hover shadow" data-header="pattern">
        <a href="#pablo" onClick={(e) => e.preventDefault()}>
          <img
            alt="..."
            className="img pattern rounded"
            src={require("assets/img/ill/p2.svg").default}
          ></img>
        </a>
        <CardBody>
          <h6 className="card-category text-danger">
            <i className="ni ni-badge"></i> head
          </h6>
          <CardTitle tag="h5">
            <a href="#pablo" onClick={(e) => e.preventDefault()}>
              제목
            </a>
          </CardTitle>
          <p className="card-description">
            내용
          </p>
        </CardBody>
      </Card>
    </>
  );
}

export default BlogCard15;
