import React from "react";

// reactstrap components
import { Button, Card, CardHeader, CardBody } from "reactstrap";

// Core Components

function ProfileCard5() {
  return (
    <>
      <Card className="card-profile card-lift--hover shadow" data-background="cover">
        <img
          alt="..."
          className="img pattern pattern-top"
          src={require("assets/img/ill/linth3.svg").default}
        ></img>
        <CardHeader>
          <div className="card-avatar">
            <a href="#pablo" onClick={(e) => e.preventDefault()}>
              <img
                alt="..."
                className="img img-raised rounded-circle"
                src={require("assets/img/faces/team-3.jpg").default}
              ></img>
            </a>
          </div>
        </CardHeader>
        <CardBody className="pt-0">
          <div className="text-center">
            <h5 className="h5 mt-3">
              닉네임
              <span className="font-weight-light">, 37</span>
            </h5>
            <div className="font-weight-300">
              <i className="ni location_pin mr-2"></i>
              부산, 동래
            </div>
          </div>
          <div className="card-profile-stats d-flex justify-content-center">
            <div>
              <span className="heading">23K</span>
              <span className="description">팔로워</span>
            </div>
            <div>
              <span className="heading">47</span>
              <span className="description">요청글</span>
            </div>
          </div>
          <Button block color="success" size="lg" type="button">
            Like!
          </Button>
        </CardBody>
      </Card>
    </>
  );
}

export default ProfileCard5;
