import React from "react";

// reactstrap components
import { Button, Card, CardHeader, CardBody, CardFooter } from "reactstrap";

// Core Components

function PricingCard6() {
  return (
    <>
      <Card
        className="card-pricing bg-gradient-warning text-center"
        data-icon="big-icon"
      >
        <CardHeader className="bg-transparent">
          <div className="icon text-white">
          <i class="fa fa-share" aria-hidden="true"></i>
          </div>
          <p className="text-white">긴급</p>
        </CardHeader>
        <CardBody>
          <div className="price d-inline">
            {/* <span className="currency text-white">$</span> */}
            <span className="amount text-white">2</span>
            <span className="frequency text-white">만</span>
          </div>
          <ul className="list-unstyled align-items-center">
            <li>
              <p className="text-sm text-white">배수구청소</p>
            </li>
            <li>
              <p className="text-sm text-white">+ 추가 000</p>
            </li>
          </ul>
        </CardBody>
        <CardFooter className="bg-transparent">
          <Button outline color="white" type="button">
            내용보기
          </Button>
        </CardFooter>
      </Card>
    </>
  );
}

export default PricingCard6;
