import React from "react";

// reactstrap components
import {
  Button,
  Card,
  CardBody,
  CardImg,
  CardTitle,
  CardText,
} from "reactstrap";

// Core Components

function BlogCard9() {
  return (
    <>
      <Card className="card-lift--hover shadow">
        <CardImg
          alt="..."
          src={require("assets/img/ill/linth3.svg").default}
          top
        ></CardImg>
        <CardBody>
          <CardTitle className="h5 mb-0">9번</CardTitle>
          <small className="text-muted">
            9번카드
          </small>
          <CardText className="mt-4">
            블로그형 카드 9번
          </CardText>
          <Button
            className="px-0"
            color="link"
            href="#none"
            onClick={(e) => e.preventDefault()}
          >
            버튼
          </Button>
        </CardBody>
      </Card>
    </>
  );
}

export default BlogCard9;
