import React from "react";
import Slider from "react-slick";
import { BrowserRouter, Route, Switch, Redirect, Link } from "react-router-dom";
// import "slick-carousel/slick/slick.css"
// import "slick-carousel/slick/slick-theme.css"

// reactstrap components
import { Container, Card, Row, Col  } from "reactstrap";

// Core Components
import NavbarSub from "components/Navbars/NavbarSub.js";
import SimpleFooter from "components/Footers/SimpleFooter.js";
import ProfileCard7 from "components/cards/ProfileCard7.js";
import ProfileCard8 from "components/cards/ProfileCard8.js";
import ProfileCard1 from "components/cards/ProfileCard1.js";
import ProfilebgSample from "assets/img/faces/atikh.jpg"

function ProfilePage() {
  React.useEffect(() => {
    document.body.classList.add("profile-page");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("profile-page");
    };
    
  });
  return (
    <>
      <NavbarSub type="transparent" />
      <div className="wrapper">
        <section className="section-profile-cover section-shaped my-0">
          <div className="page-header header-filter">
              <div className="content-center">
                <Row>
                  <Col md="12">
                    <div className="glide">
                      <h1>에어컨 청소 해주실 분 모십니다.</h1>
                      <div className="glide__track" data-glide-el="track">
                        <ul className="glide__slides">
                          <li className="glide__slide">
                            <img
                              alt="..."
                              src={require("assets/img/pages/kevin-grieve.jpg").default}
                            ></img>
                          </li>
                          <li className="glide__slide">
                            <img
                              alt="..."
                              src={require("assets/img/pages/mohamed.jpg").default}
                            ></img>
                          </li>
                          <li className="glide__slide">
                            <img
                              alt="..."
                              src={require("assets/img/pages/nathan-dumlao.jpg").default}
                            ></img>
                          </li>
                          <li className="glide__slide">
                            <img
                              alt="..."
                              src={require("assets/img/pages/nicolas-prieto.jpg").default}
                            ></img>
                          </li>
                          <li className="glide__slide">
                            <img
                              alt="..."
                              src={require("assets/img/pages/sample_bg.jpg").default}
                            ></img>
                          </li>
                        </ul>
                      </div>
                      <div className="glide__arrows" data-glide-el="controls">
                        <button
                          className="glide__arrow glide__arrow--left"
                          data-glide-dir="<"
                        >
                          {/* <i className="ni ni-bold-left"></i> */}
                        </button>
                        <button
                          className="glide__arrow glide__arrow--right"
                          data-glide-dir=">"
                        >
                          {/* <i className="ni ni-bold-right"></i> */}
                        </button>
                      </div>
                    </div>
                  </Col>
                </Row>
              </div>
          </div>
        </section>
        <section className="section bg-white">
          <Container>
            <ProfileCard8 />
          </Container>
        </section>
        <SimpleFooter />
      </div>
    </>
  );
}

export default ProfilePage;
