import React from "react";

// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  ListGroupItem,
  ListGroup,
  Row,
  Col,
} from "reactstrap";

// Core Components

function PricingCard5() {
  return (
    <>
      <Card
        className="card-pricing card-coin"
        style={{
          backgroundImage:
            "url(" + require("assets/img/ill/pattern_pricing5.svg").default + ")",
        }}
      >
        <CardHeader>
          <img
            alt="..."
            className="img-center shadow"
            src={require("assets/img/dropbox.png").default}
          ></img>
        </CardHeader>
        <CardBody>
          <Row>
            <Col className="text-center" md="12">
              <h5 className="text-uppercase">이사청소</h5>
              <span>가격협의</span>
              <hr className="bg-info"></hr>
            </Col>
          </Row>
          <Row>
            <ListGroup>
              <ListGroupItem>200,000원부터</ListGroupItem>
              <ListGroupItem>전국구</ListGroupItem>
              <ListGroupItem>마감 3일전</ListGroupItem>
            </ListGroup>
          </Row>
        </CardBody>
        <CardFooter className="text-center bg-transparent">
          <Button className="mb-3" color="primary">
            바로신청
          </Button>
        </CardFooter>
      </Card>
    </>
  );
}

export default PricingCard5;
