import React from "react";
import { Link } from "react-router-dom";
import {UserAPI} from "./userAPI";

// reactstrap components
import {
  Button,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Container,
  NavLink
} from "reactstrap";

// Core Components
import NavbarMain from "components/Navbars/NavbarMain.js";
import SimpleFooter from "components/Footers/SimpleFooter";
import ResetPage from "views/ResetPage";
import { nodeName } from "jquery";

function RegisterPage() {
  const [activeContainer, setActiveContainer] = React.useState("");
  const [signupNameFocus, setSignupNameFocus] = React.useState("");
  const [signupEmailFocus, setSignupEmailFocus] = React.useState("");
  const [signupPasswordFocus, setSignupPasswordFocus] = React.useState("");
  const [signinEmailFocus, setSigninEmailFocus] = React.useState("");
  const [signinPasswordFocus, setSigninPasswordFocus] = React.useState("");
  const [email, setemail] = React.useState("");
  const [password,setpassword]= React.useState("");
  const [repassword,setrepassword]= React.useState("");

  let user = new UserAPI('','')

  React.useEffect(() => {
    document.body.classList.add("register-page");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("register-page");
    };
  }, []);

  let emailhandleChange = (e) => {
    console.log(e.target.value)
    setemail(e.target.value)
  }
  let passwordhandleChange = (e) => {
    console.log(e.target.value)
    setpassword(e.target.value)
  }
  let repasswordhandleChange = (e) => {
    console.log(e.target.value)
    setrepassword(e.target.value)
  }

  return (
    <>
      <NavbarMain type="transparent" />
      <div className="wrapper">
        <div className="page-header bg-default">
          <div
            className="page-header-image"
            // style={{backgroundImage:"url(" + require("assets/img/ill/register_bg.png").default + ")",}}
            
          ></div>
          <Container className={activeContainer}>
            {/* 회원가입박스 */}
            <div className="form-container sign-up-container">
              <Form>
                <h2>계정 만들기</h2>
                <div className="social-container d-none">
                  <Button color="facebook" size="sm" type="button">
                    <span className="btn-inner--icon">
                      <i className="fab fa-facebook"></i>
                    </span>
                  </Button>
                  <Button color="instagram" size="sm" type="button">
                    <span className="btn-inner--icon">
                      <i className="fab fa-instagram"></i>
                    </span>
                  </Button>
                  <Button color="twitter" size="sm" type="button">
                    <span className="btn-inner--icon">
                      <i className="fab fa-twitter"></i>
                    </span>
                  </Button>
                </div>
                <span className="text-default mb-4">
                  지금 가입하세요!
                </span>
                <FormGroup className={"mb-3 " + signupNameFocus}>
                  <InputGroup className="input-group-alternative">
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <i className="ni ni-circle-08"></i>
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      placeholder="닉네임"
                      type="text"
                      onFocus={() => setSignupNameFocus("focused")}
                      onBlur={() => setSignupNameFocus("")}
                    ></Input>
                  </InputGroup>
                </FormGroup>
                <FormGroup className={"mb-3 " + signupEmailFocus}>
                  <InputGroup className="input-group-alternative">
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <i className="ni ni-email-83"></i>
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      placeholder="이메일"
                      type="email"
                      onFocus={() => setSignupEmailFocus("focused")}
                      onBlur={() => setSignupEmailFocus("")}
                      onChange={emailhandleChange}
                    ></Input>
                  </InputGroup>
                </FormGroup>
                <FormGroup className={signupPasswordFocus}>
                  <InputGroup className="input-group-alternative">
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <i className="ni ni-lock-circle-open"></i>
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      placeholder="비밀번호"
                      type="password"
                      onFocus={() => setSignupPasswordFocus("focused")}
                      onBlur={() => setSignupPasswordFocus("")}
                      onChange={passwordhandleChange}
                    ></Input>
                  </InputGroup>
                </FormGroup>
                <FormGroup className={signupPasswordFocus}>
                  <InputGroup className="input-group-alternative">
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <i className="ni ni-lock-circle-open"></i>
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      placeholder="비밀번호확인"
                      type="repassword"
                      onFocus={() => setSignupPasswordFocus("focused")}
                      onBlur={() => setSignupPasswordFocus("")}
                      onChange={repasswordhandleChange}
                    ></Input>
                  </InputGroup>
                  <div className="text-danger mb-4" style={{zIndex:"100"}}>
                  {
                    password !== repassword
                    ? <sapn>비밀번호가 다릅니다.</sapn>
                    : null
                  }
                </div>
                </FormGroup>
                <Button color="primary"
                  onClick={() => {
                    console.log()
                      user.setEmail(email);
                      user.setPassword(password);
                      user.setrePassword(repassword);
                      user.signUp();
                        }}>회원가입
                </Button>
              </Form>
            </div>
            {/* 로그인박스 */}
            <div className="form-container sign-in-container login">
              <Form action="#" role="form">
                <h2>로그인</h2>
                <div className="social-container d-none">
                  <Button color="facebook" size="sm" type="button">
                    <span className="btn-inner--icon">
                      <i className="fab fa-facebook"></i>
                    </span>
                  </Button>
                  <Button color="instagram" size="sm" type="button">
                    <span className="btn-inner--icon">
                      <i className="fab fa-instagram"></i>
                    </span>
                  </Button>
                  <Button color="twitter" size="sm" type="button">
                    <span className="btn-inner--icon">
                      <i className="fab fa-twitter"></i>
                    </span>
                  </Button>
                </div>
                <span className="text-default mb-4">홈클린 회원이신가요?</span>
                <FormGroup className={"mb-3 " + signinEmailFocus}>
                  <InputGroup className="input-group-alternative">
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                      <i className="fa fa-envelope-o" aria-hidden="true"></i>
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      placeholder="이메일"
                      type="email"
                      onFocus={() => setSigninEmailFocus("focused")}
                      onBlur={() => setSigninEmailFocus("")}
                      onChange={emailhandleChange}
                    ></Input>
                  </InputGroup>
                </FormGroup>
                <FormGroup className={signinPasswordFocus}>
                  <InputGroup className="input-group-alternative">
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                      <i className="fa fa-unlock-alt" aria-hidden="true"></i>
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      placeholder="비밀번호"
                      type="password"
                      onFocus={() => setSigninPasswordFocus("focused")}
                      onBlur={() => setSigninPasswordFocus("")}
                      onChange={passwordhandleChange}
                    ></Input>
                  </InputGroup>
                </FormGroup>
                <div className="custom-control custom-control-alternative custom-checkbox">
              <input
                className="custom-control-input"
                id=" customCheckLogin2"
                type="checkbox"
              ></input>
              <label
                className="custom-control-label"
                htmlFor=" customCheckLogin2"
              >
                <span className="text-default opacity-5">ID기억하기</span>
              </label>
            </div>

                {/* <p> 비밀번호를 잊으셨나요?!</p> */}

                <NavLink
                  className=""
                  to="/ResetPage"
                  tag={Link}
                  id=""
                >
                  {/* <i class="fa fa-power-off" aria-hidden="true"></i> */}
                  <span className="nav-link-inner--text ml-2">
                      비빌번호를 잊으셨나요?
                  </span>
                </NavLink>

                {/* <Button className="mt-4" color="primary" to="/resetPage" tag={Link}>
                  비밀번호 찾기
                </Button> */}

                <Button className="mt-3" color="primary"
                  onClick={() => {
                    console.log()
                      user.setEmail(email);
                      //  console.log(e.target.value);
                      user.setPassword(password);
                      user.signIn();
                        }}>로그인
                </Button>
              </Form>
            </div>
            <div className="overlay-container">
              <div className="overlay">
                <div className="overlay-panel overlay-left">
                  <h1>로그인</h1>
                  <p>
                    내 계정으로 로그인 할께요
                  </p>
                  <Button
                    className="btn-primary"
                    // color="primary"
                    id="signIn"
                    size="sm"
                    onClick={() => setActiveContainer("")}
                  >
                    로그인 하기
                  </Button>
                </div>
                <div className="overlay-panel overlay-right">
                  <h1>회원가입</h1>
                  <p>정보를 입력하고 회원으로 가입할게요</p>
                  <Button
                    className="btn-primary"
                    // color="primary"
                    id="signUp"
                    size="sm"
                    onClick={() => setActiveContainer("right-panel-active")}
                  >
                    회원가입 하기
                  </Button>
                </div>
              </div>
            </div>
          </Container>
        </div>
        {/* <SimpleFooter /> */}
      </div>
    </>
  );
}

export default RegisterPage;
