import React from "react";

// reactstrap components
import { Card, CardImg, CardImgOverlay, CardTitle, CardText } from "reactstrap";

// Core Components

function BlogCard4() {
  return (
    <>
      <Card className="text-white bg-default card-lift--hover shadow">
        <CardImg alt="..." src={require("assets/img/ill/inn.svg").default}></CardImg>
        <CardImgOverlay className="d-flex align-items-center">
          <div className="content">
            <CardTitle className="h2 text-white mb-2">4번</CardTitle>
            <p className="card-description">
              블로그형 카드 4번
            </p>
            <CardText className="text-sm font-weight-bold">
              블로그 디자인 카드 4번
            </CardText>
          </div>
        </CardImgOverlay>
      </Card>
    </>
  );
}

export default BlogCard4;
