/*!

Homeclean Nav

*/
import React from "react";
import { Link } from "react-router-dom";
// JavaScript plugin that hides or shows a component based on your scroll
import Headroom from "headroom.js";
import {UserAPI} from "../../views/userAPI";
// reactstrap components
import {
  Button,
  UncontrolledCollapse,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
  UncontrolledDropdown,
  Media,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  Container,
  Row,
  Col,
  UncontrolledTooltip
} from "reactstrap";


class DemoNavbar extends React.Component {
  componentDidMount() {
    let headroom = new Headroom(document.getElementById("navbar-main"));
    // initialise
    headroom.init();
  }
  state = {
    collapseClasses: "",
    collapseOpen: false,
    email : this.email
  };

  onExiting = () => {
    this.setState({
      collapseClasses: "collapsing-out"
    });
  };

  onExited = () => {
    this.setState({
      collapseClasses: ""
    });
  };
  logout() {
    localStorage.clear();
    window.location.href = '/';
}
  render() {
    let user = new UserAPI(this.email, this.password)
    return (
      <>
        <header className="header-global">
          <Navbar
            className="navbar-main navbar navbar-light headroom"
            expand="lg"
            id="navbar-main"
          >
            <Container>
              <NavbarBrand className="mr-lg-5" to="/" tag={Link}>
                {/* <img alt="..." src={require("../../assets/img/brand/logo_sample_white.png").default}
                className="yestop"
                /> */}
                <h1 className="yestop">paprika</h1>
                {/* <img alt="..." src={require("../../assets/img/brand/logo_sample_black.png").default}
                className="notop"
                /> */}
                <h1 className="notop">paprika</h1>
              </NavbarBrand>
              <button className="navbar-toggler" id="navbar_global">
                <span className="navbar-toggler-icon" />
              </button>
              <UncontrolledCollapse
                toggler="#navbar_global"
                navbar
                className={this.state.collapseClasses}
                onExiting={this.onExiting}
                onExited={this.onExited}
              >
                <div className="navbar-collapse-header">
                  <Row>
                    <Col className="collapse-brand" xs="6">
                      <Link to="/">
                        {/* <img alt="homeclean" src={require("../../assets/img/brand/logo_sample_black.png").default} /> */}
                        PAPRIKA
                      </Link>
                    </Col>
                    <Col className="collapse-close" xs="6">
                      <button className="navbar-toggler" id="navbar_global">
                        <span />
                        <span />
                      </button>
                    </Col>
                  </Row>
                </div>

                <Nav className="navbar-nav-hover align-items-lg-center ml-lg-auto" navbar>
                  <NavItem>
                    <NavLink
                      to="/Help"
                      tag={Link}
                    >
                      HELP
                    </NavLink>
                  </NavItem>
                  
                  { user === null
                    ? <NavItem><NavLink to="/RegisterPage" tag={Link}>로그인</NavLink></NavItem>
                    : <NavItem><NavLink to="/RegisterPage" tag={Link}>로그아웃</NavLink></NavItem>
                  }
                  
                  <UncontrolledDropdown nav>
                    <DropdownToggle nav>
                      <i className="ni ni-ui-04 d-lg-none mr-1" />
                      <span className="nav-link-inner--text">프로필</span>
                    </DropdownToggle>
                    <DropdownMenu className="dropdown-menu-xl">
                      <div className="dropdown-menu-inner">
                        <Media
                          className="d-flex align-items-center"
                          href="#none"
                        >
                          <div className="icon icon-shape bg-gradient-primary rounded-circle text-white">
                            <i className="ni ni-spaceship" />
                          </div>
                          <Media body className="ml-3">
                            <h6 className="heading text-primary mb-md-1">
                              헬프
                            </h6>
                            <p className="description d-md-inline-block mb-0">
                              도움이 필요한 사람들
                            </p>
                          </Media>
                        </Media>
                        <Media
                          className="d-flex align-items-center"
                          href="#none"
                        >
                          <div className="icon icon-shape bg-gradient-success rounded-circle text-white">
                            <i className="ni ni-palette" />
                          </div>
                          <Media body className="ml-3">
                            <h6 className="heading text-primary mb-md-1">
                              해결사
                            </h6>
                            <p className="description d-md-inline-block mb-0">
                              도움을 주는 사람들
                            </p>
                          </Media>
                        </Media>
                        <Media
                          className="d-flex align-items-center"
                          href="#none"
                        >
                          <div className="icon icon-shape bg-gradient-warning rounded-circle text-white">
                            <i className="ni ni-ui-04" />
                          </div>
                          <Media body className="ml-3">
                            <h5 className="heading text-warning mb-md-1">
                              내용
                            </h5>
                            <p className="description d-md-inline-block mb-0">
                              내용
                            </p>
                          </Media>
                        </Media>
                      </div>
                    </DropdownMenu>
                  </UncontrolledDropdown>
                  <UncontrolledDropdown nav>
                    <DropdownToggle nav>
                      <i className="ni ni-collection d-lg-none mr-1" />
                      <span className="nav-link-inner--text">Page 정리</span>
                    </DropdownToggle>
                    <DropdownMenu>
                      <DropdownItem to="/LoginPage" tag={Link}>
                        Login
                      </DropdownItem>
                      <DropdownItem to="/ProfilePage" tag={Link}>
                        Profile
                      </DropdownItem>
                      <DropdownItem to="/RegisterPage" tag={Link}>
                        Join 
                      </DropdownItem>
                      <DropdownItem to="/AccountSettings" tag={Link}>
                        MyPage
                      </DropdownItem>
                      <DropdownItem to="/ProfileWrite" tag={Link}>
                        Write
                      </DropdownItem>
                    </DropdownMenu>
                  </UncontrolledDropdown>
                </Nav>

                <Nav className="align-items-lg-center">
                  <NavItem className="">
                    <NavLink
                      className="nav-link-icon"
                      to="/RegisterPage"
                      tag={Link}
                      id="tooltip184698705"
                    >
                      <i class="fa fa-power-off" aria-hidden="true"></i>
                      <span className="nav-link-inner--text d-lg-none ml-2">
                        Login
                      </span>
                    </NavLink>
                    <UncontrolledTooltip delay={0} target="tooltip184698705">
                      로그인
                    </UncontrolledTooltip>
                  </NavItem>
                  
                  <NavItem className="d-none ml-lg-4">
                    <Button
                      className="btn-icon"
                      to="/AccountSettings" 
                      tag={Link}
                    >
                      {/* <span className="btn-inner--icon">
                        <i className="fa fa-cloud-download mr-2" />
                      </span> */}
                      <span className="nav-link-inner--text ml-1">
                        마이페이지1
                      </span>
                    </Button>
                  </NavItem>
                </Nav>

              </UncontrolledCollapse>
            </Container>
          </Navbar>
        </header>
      </>
    );
  }
}

export default DemoNavbar;
