import React from "react";

// reactstrap components
import { Button, Card, CardBody, CardTitle } from "reactstrap";

// Core Components

function PricingCard3() {
  return (
    <>
      <Card
        className="card-pricing card-background card-lift--hover shadow border-0"
        style={{
          backgroundImage:
            "url(" + require("assets/img/ill/pattern_pricing6.svg").default + ")",
        }}
      >
        <CardBody className="pb-4">
          <h6 className="card-category text-danger text-uppercase">긴급</h6>
          <CardTitle className="text-default" tag="h1">
            120,000<small>원</small>
          </CardTitle>
          <ul>
            <li>
              <div className="d-flex align-items-center">
                <div>
                  <div className="icon icon-xs icon-shape icon-shape-info shadow rounded-circle">
                    <i className="ni ni-check-bold"></i>
                  </div>
                </div>
                <div>
                  <span className="pl-2 text-sm text-whit">에어컨</span>
                </div>
              </div>
            </li>
            <li>
              <div className="d-flex align-items-center">
                <div>
                  <div className="icon icon-xs icon-shape icon-shape-info shadow rounded-circle">
                    <i className="ni ni-check-bold"></i>
                  </div>
                </div>
                <div>
                  <span className="pl-2 text-sm text-whit">학교</span>
                </div>
              </div>
            </li>
            <li>
              <div className="d-flex align-items-center">
                <div>
                  <div className="icon icon-xs icon-shape icon-shape-danger shadow rounded-circle">
                    <i className="ni ni-fat-remove"></i>
                  </div>
                </div>
                <div>
                  <span className="pl-2 text-sm text-whit">
                    50대 설치
                  </span>
                </div>
              </div>
            </li>
            <li>
              <div className="d-flex align-items-center">
                <div>
                  <div className="icon icon-xs icon-shape icon-shape-danger shadow rounded-circle">
                    <i className="ni ni-fat-remove"></i>
                  </div>
                </div>
                <div>
                  <span className="pl-2 text-sm text-whit"></span>
                </div>
              </div>
            </li>
          </ul>
          <Button
            className="mt-4"
            color="danger"
            href="#pablo"
            onClick={(e) => e.preventDefault()}
          >
            지금 신청
          </Button>
        </CardBody>
      </Card>
    </>
  );
}

export default PricingCard3;
