import React from "react";

// reactstrap components
import { Container, Row, Col } from "reactstrap";

// Core Components
import NavbarMain from "components/Navbars/NavbarMain.js";
import DemoFooter from "components/Footers/DemoFooter.js";
import LoginCard1 from "components/cards/LoginCard1.js";

function LoginPage() {
  React.useEffect(() => {
    document.body.classList.add("login-page");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("login-page");
    };
  });
  return (
    <>
      <NavbarMain type="transparent" />
      <div className="section-shaped my-0 skew-separator skew-mini">
        <div className="page-header page-header-mini header-filter">
          <div
            className="page-header-image"
            // style={{
            //   backgroundImage:
            //     "url(" + require("assets/img/sections/sky_1.jpg").default + ")",
            // }}
            style={{ background: "#41CC7E" ,}}
          ></div>
          <Container>
            <div className="header-body text-center">
              <Row className="justify-content-center">
                <Col className="px-5 mt-7" lg="6" md="8" xl="5">
                  <h1 className="text-white">로그인</h1>
                  <p className="text-lead text-white">
                    로그인을 하시면 ...
                  </p>
                </Col>
              </Row>
            </div>
          </Container>
        </div>
      </div>
      <section className="upper">
        <Container>
          <Col className="mx-auto" lg="5" md="8">
            <LoginCard1 />
          </Col>
        </Container>
      </section>
      <DemoFooter />
    </>
  );
}

export default LoginPage;
