import React from "react";

// reactstrap components
import {
  Badge,
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  FormGroup,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  ListGroupItem,
  ListGroup,
  Media,
  Row,
  Col,
  UncontrolledTooltip,
} from "reactstrap";

// Core Components

function Chat() {
  const [searchFocus, setSearchFocus] = React.useState("");
  const [messageFocus, setMessageFocus] = React.useState("");
  return (
    <>
      <Row className="flex-row chat">
        <Col lg="12">
          <Card>
            <CardHeader className="d-inline-block">
              <Row>
                <Col md="10">
                  <Media className="align-items-center">
                    <img
                      alt="..."
                      className="avatar shadow"
                      src={require("assets/img/faces/cartoon.png").default}
                    ></img>
                    <Media body>
                      <h6 className="mb-0 d-block">닉네임</h6>
                      <span className="text-muted text-small">
                        마지막접속시간 today at 1:53am
                      </span>
                    </Media>
                  </Media>
                </Col>
                <Col md="1" xs="3">
                  <Button
                    className="btn-text"
                    color="link"
                    data-placement="top"
                    id="tooltip558026681"
                    type="button"
                  >
                    <i className="ni ni-book-bookmark"></i>
                  </Button>
                  <UncontrolledTooltip
                    delay={0}
                    placement="top"
                    target="tooltip558026681"
                  >
                    Video call
                  </UncontrolledTooltip>
                </Col>
                <Col md="1" xs="3">
                  <UncontrolledDropdown>
                    <Button
                      className="text-primary"
                      color="link"
                      data-toggle="dropdown"
                      type="button"
                    >
                      <i className="ni ni-settings-gear-65"></i>
                    </Button>
                    <DropdownMenu right>
                      <DropdownItem
                        href="#pablo"
                        onClick={(e) => e.preventDefault()}
                      >
                        <i className="ni ni-single-02"></i>
                        Profile
                      </DropdownItem>
                      <DropdownItem
                        href="#pablo"
                        onClick={(e) => e.preventDefault()}
                      >
                        <i className="ni ni-notification-70"></i>
                        Mute conversation
                      </DropdownItem>
                      <DropdownItem
                        href="#pablo"
                        onClick={(e) => e.preventDefault()}
                      >
                        <i className="ni ni-key-25"></i>
                        Block
                      </DropdownItem>
                      <DropdownItem
                        href="#pablo"
                        onClick={(e) => e.preventDefault()}
                      >
                        <i className="ni ni-button-power"></i>
                        Clear chat
                      </DropdownItem>
                      <DropdownItem divider></DropdownItem>
                      <DropdownItem
                        href="#pablo"
                        onClick={(e) => e.preventDefault()}
                      >
                        <i className="ni ni-fat-remove"></i>
                        Delete chat
                      </DropdownItem>
                    </DropdownMenu>
                  </UncontrolledDropdown>
                </Col>
              </Row>
            </CardHeader>
            <CardBody>
              <Row className="justify-content-start">
                <Col className="col-auto">
                  <Card className="bg-gradient-white">
                    <CardBody className="p-2">
                      <p className="mb-1">
                        안녕하세요
                      </p>
                      <div>
                        <small className="opacity-60">
                          <i className="far fa-clock"></i>
                          3:14am
                        </small>
                      </div>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <Row className="justify-content-end text-right">
                <Col className="col-auto">
                  <Card className="bg-red text-white">
                    <CardBody className="p-2">
                      <p className="mb-1">
                        파프리카에서 보고 연락드렸습니다.<br></br>
                      </p>
                      <div>
                        <small className="opacity-60">3:30am</small>
                        <i className="ni ni-check-bold"></i>
                      </div>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <Row className="mt-4">
                <Col className="text-center" md="12">
                  <Badge className="text-default">Wed, 3:27pm</Badge>
                </Col>
              </Row>
              <Row className="justify-content-start">
                <Col className="col-auto">
                  <Card className="bg-gradient-white">
                    <CardBody className="p-2">
                      <p className="mb-1">
                        아 네네!
                      </p>
                      <div>
                        <small className="opacity-60">
                          <i className="far fa-clock"></i>
                          4:31pm
                        </small>
                      </div>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <Row className="justify-content-end text-right">
                <Col className="col-auto">
                  <Card className="bg-red text-white">
                    <CardBody className="p-2">
                      <p className="mb-1">제가 할께요</p>
                      <div>
                        <small className="opacity-60">4:40pm</small>
                        <i className="ni ni-check-bold"></i>
                      </div>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <Row className="justify-content-start">
                <Col className="col-auto">
                  <Card className="bg-gradient-white">
                    <CardBody className="p-2">
                      <p className="mb-1">
                        아 진짜요? 5,000원밖에 없는데 괜찮을까요?ㅠㅠ
                      </p>
                      <div>
                        <small className="opacity-60">
                          <i className="far fa-clock"></i>
                          4:42pm
                        </small>
                      </div>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <Row className="justify-content-end text-right">
                <Col className="col-auto">
                  <Card className="bg-red text-white">
                    <CardBody className="p-2">
                      <p className="mb-1">
                        전혀 문제없습니다. 야무지게 처리하겠습니다.믿고 맡겨주세요
                      </p>
                      <div>
                        <small className="opacity-60">4:46pm</small>
                        <i className="ni ni-check-bold"></i>
                      </div>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <Row className="justify-content-start">
                <Col xs="5">
                  <Card className="bg-gradient-white">
                    <CardBody className="p-2">
                      <Col className="p-0" xs="12">
                        <img
                          alt="..."
                          className="img-fluid rounded mb-1"
                          src={require("assets/img/theme/img-1-1200x1000.jpg").default}
                        ></img>
                      </Col>
                      <p className="mb-1">
                        이쪽으로 와주세요
                      </p>
                      <div>
                        <small className="opacity-60">
                          <i className="far fa-clock"></i>
                          4:47pm
                        </small>
                      </div>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <Row className="justify-content-end text-right">
                <Col className="col-auto">
                  <Card className="bg-red text-white">
                    <CardBody className="p-2">
                      <p className="mb-0">
                        ㅇㅋㄷㅋ ok yo 지금 갑니다.
                      </p>
                      <div>
                        <small className="opacity-60">4:47pm</small>
                        <i className="ni ni-check-bold"></i>
                      </div>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <Row className="justify-content-start">
                <Col className="col-auto">
                  <Card>
                    <CardBody className="p-2">
                      <div className="spinner">
                        <div className="bounce1"></div>
                        <div className="bounce2"></div>
                        <div className="bounce3"></div>
                      </div>
                      <p className="d-inline-block mr-2 mb-1 mt-1">입력중...</p>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
            </CardBody>
            <CardFooter className="d-none">
              <FormGroup className={messageFocus}>
                <InputGroup className="mb-4">
                  <Input
                    placeholder="Your message"
                    type="text"
                    onFocus={() => setMessageFocus("focused")}
                    onBlur={() => setMessageFocus("")}
                  ></Input>
                  <InputGroupAddon addonType="append">
                    <InputGroupText>
                      <i className="ni ni-send"></i>전송
                    </InputGroupText>
                  </InputGroupAddon>
                </InputGroup>
              </FormGroup>
            </CardFooter>
          </Card>
        </Col>
      </Row>
    </>
  );
}

export default Chat;
